body {
line-height:1.7em;
}
p {
padding-bottom:15px;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #000000;
}

.label {
padding-top:10px;
padding-bottom:3px;
font-weight:bold;
font-size:30px;
}
hr {
margin:20px 0;
height:1px;
border:none;
background:#ccc;
}
input[type=text] {
    box-sizing:border-box;
    padding:7px;
    font-size:20px;
    display:block;
    width:100%;
}
.result {
    padding:7px;
    font-size:20px;
    border:1px dashed #ccc;
    height:40px;
}

.button {
    display:block;
    padding:20px 7px;
    border:1px solid #ccc;
    border-radius:3px;
    font-size:20px;
    text-align:center;
    margin-bottom:10px;
}

/* airport calculator */
    .autocomplete {
      position: relative;
      display: block;
      margin: 0 auto;
      width: 100%;
    }
    ul {
      width: 100%;
      position: absolute;
      z-index: 100;
      list-style: none;
      margin: 0 auto;
      padding: 0;
      background: white;
      max-height: 140px;
      overflow: scroll;
      border:1px solid #ccc;
    }

    li {
      cursor: pointer;
      font-size: 14px;
      padding: 10px 5px;
      display: block;
      font-family: 'Roboto', sans-serif;
    }

    li:hover {
      background: #d5d9e0;
    }

    label {
      margin-top: 20px;
      display: block;
      font-family: 'Roboto', sans-serif;
      font-weight: bold;
    }
/* end */
